.Wrap {
    overflow: hidden;
}

.header {
    line-height: 100px;
    border-bottom: 1px solid #ddd;

    .inner {
        display: flex;
        justify-content: space-between;
        width: 1200px;
        margin: 0 auto;

        h1 {
            font-size: 0;

            img {
                height: 100px;
            }
        }

        .gnb {
            &>ul {
                display: flex;
                gap: 40px;

                &>li {
                    position: relative;
                    text-align: center;
                    font-size: 18px;

                    &>a {
                        display: block;
                    }

                    ul {
                        position: absolute;
                        top: 100px;
                        left: 50%;
                        transform: translate(-50%, 0);
                        display: none;
                        background: #fff;
                        z-index: 999;

                        &>li {
                            width: 150px;
                            line-height: 40px;
                            font-size: 14px;

                            a {
                                display: block;
                            }

                            &:hover>a {
                                background: #555;
                                color: #fff;
                            }

                        }

                        li~li {
                            border-top: 1px solid #ddd;
                        }
                    }
                }

                &>li:hover {
                    ul {
                        display: block;

                    }
                }
            }
        }

        .h_link {
            font-size: 20px;
            cursor: pointer;

            &>* {
                margin: 0 0 0 10px;
            }

            .cart {
                vertical-align: 2px;
            }
        }
    }
}

.MainVisual {
    position: relative;

    .slick-track,
    .slick-list {
        transition-delay: 0.03s;
    }

    .itm {
        height: 700px;
    }

    @for $i from 1 to 4 {
        .itm0#{$i} {
            background: url(../../public/image/mainvisual0#{$i}.jpg) no-repeat center center/cover;
        }
    }

    .arrows {
        color: #000;
        font-size: 150px;
        cursor: pointer;

        .left {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);

            margin: 0 0 0 -750px;
        }

        .right {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);

            margin: 0 0 0 750px;
        }
    }

    .dots {
        position: absolute;
        bottom: 100px;
        left: 50%;
        transform: translate(-50%, 0);

        button {
            border: none;
            background: #fff;
            width: 20px;
            height: 20px;
            font-size: 0;
            margin: 0 3px;
            border-radius: 50%;
            cursor: pointer;

            &.on {
                background: tomato;
            }
        }
    }
}

.MainContents {
    padding: 100px 0;

    .title {
        text-align: center;
        margin: 0 0 30px 0;

        .MainConHand {
            font-size: 70px;
            margin: 0 0 10px 0;
            color: #b9002c;
        }

        h2 {
            font-size: 35px;
            font-weight: 700;
        }
    }

    .inner {
        width: 1200px;
        margin: 0 auto;

        .line {
            position: relative;
            margin: 0 0 30px 0;
            height: 1px;
        }

        .line::before {
            content: "";
            position: absolute;
            top: 50%;
            transform: translate(0, -50%);
            width: 100%;
            height: 1px;
            background: #b9002c;
        }

        .line::after {
            content: "";
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            width: 10px;
            height: 10px;
            background: #b9002c;
            border-radius: 50%;
        }

        .MainConSlide {
            margin: 0 -14px 30px -15px;
            text-align: center;

            .itm {
                padding: 0 15px;

                figure {
                    margin: 0 0 20px 0;
                }

                p {
                    font-size: 18px;
                }
            }
        }

        .dots {
            text-align: center;

            button {
                border: none;
                background: rgba(0, 0, 0, 0.5);
                margin: 0 3px;
                font-size: 0;
                width: 15px;
                height: 15px;
                border-radius: 50%;

                cursor: pointer;

                &.on {
                    background: tomato;
                }
            }
        }

        .SlideWrapper {
            position: relative;

            .arrows {
                cursor: pointer;
                font-size: 30px;

                .left {
                    position: absolute;
                    top: 100px;
                    left: 50%;
                    transform: translate(-50%, -50%);
                    margin: 0 0 0 -650px;
                }

                .right {
                    position: absolute;
                    top: 100px;
                    left: 50%;
                    transform: translate(-50%, -50%);
                    margin: 0 0 0 650px;
                }
            }
        }
    }
}

.MainMovie {
    padding: 100px 0;
    background: #f9f9f9;

    .inner {
        width: 1200px;
        margin: 0 auto;
    }
}

.MainProduct {
    position: relative;
    padding: 100px 0;
    background: #eedede;

    .inner {
        display: flex;
        width: 1200px;
        margin: 0 auto;
        gap: 30px;


        .logo_box {
            flex: 3;
            z-index: 999;

            figure {}


            img {
                max-width: 100%;
                height: 100%;
                margin: -13px 0 0 -17px;
            }
        }

        .info_box {
            flex: 4;
            z-index: 999;
            padding: 0 0 0 0;

            strong {
                display: block;
                font-size: 30px;
                font-weight: 700;
                margin: 0 0 10px 0;
            }

            span {
                display: block;
                font-size: 35px;
                font-weight: 500;
                margin: 0 0 30px 0;
            }

            p {
                word-break: keep-all;
                font-size: 18px;
                font-weight: 500;
                line-height: 26px;
                margin: 0 0 30px 0;
            }

            button {
                border: none;
                background: none;

                a {
                    display: block;
                    background: #333;
                    color: #fff;
                    padding: 10px 30px;
                }
            }
        }

        .input_box {
            flex: 3;
            z-index: 999;

            .input {
                margin: 0 0 20px 0;

                p {
                    font-weight: 500;
                    margin: 0 0 5px 1px;
                }

                input {
                    outline: none;
                    border: none;
                    width: 100%;
                    padding: 5px 10px;
                }

                .Finput {
                    display: flex;
                    gap: 15px;

                }
            }

            .input_desc {
                margin: 0 0 20px 0;

                textarea {
                    display: block;
                    width: 100%;
                    height: 50px;
                    border: none;
                    outline: none;
                    resize: none;
                    padding: 5px 10px;
                }


            }

            span {
                display: block;


                a {
                    display: inline-block;
                    padding: 10px 20px;
                    background: #333;

                    color: #fff;
                }
            }


        }
    }

}

.MainProduct::after {
    content: "";
    position: absolute;
    inset: 0 0 0 0;
    background: url(../../public/image/snow1.png);
}

.MainInfo {
    padding: 100px 0;

    .inner {
        width: 1200px;
        margin: 0 auto;

        .info_text {
            margin: 0 0 50px 0;

            ul {
                display: flex;
                gap: 30px;

                li {
                    flex: 1;
                    text-align: center;

                    i {
                        display: block;
                        font-size: 50px;
                        margin: 0 0 20px 0;
                        color: #b9002c;

                        &>a:hover {
                            color: #996975
                        }
                    }

                    strong {
                        display: block;
                        font-size: 25px;
                        font-weight: 700;
                        margin: 0 0 10px 0;
                    }

                    p {
                        font-size: 15px;
                        line-height: 23px;
                    }
                }
            }
        }

    }
}

.Footer {
    padding: 30px 0 50px 0;
    background: #333;
    color: #fff;

    .inner {
        width: 1200px;
        margin: 0 auto;

        .footer_top {
            display: flex;
            justify-content: space-between;
            line-height: 30px;
            margin: 0 0 20px 0;

            .list {
                display: flex;
                gap: 10px;
                font-size: 14px;

                .bold {
                    font-weight: 700;
                }
            }

            .sns {
                display: flex;
                gap: 15px;
                font-size: 25px;

                li {
                    a {
                        display: block;
                    }
                }
            }
        }

        .footer_bottom {
            display: flex;
            justify-content: space-between;
            padding: 30px 0 0 0;
            border-top: 1px solid #666;
            font-size: 14px;

            figure {
                width: 70px;
                height: 70px;

                img {
                    height: 100%;
                }
            }
        }
    }
}